/**
 * Valide les donnes pour la cration d'un nouveau produit
 * @param {string} designation Dsignation du produit
 * @param {string} unite Nature du produit
 * @param {number} marge Marge bnficiaire
 * @param {number} stockSecurite Stock de scurit
 * @returns Object of {boolean valid value and an objet of errrors}
 */
export const validerNouveauProduit = (
	designation,
	unite,
	marge,
	qMin,
	code,
	numCompte
) => {
	let valid = true;
	const errors = {};

	if (!numCompte || numCompte === "" || numCompte[0] !== "3") {
		valid = false;
		errors["numCompte"] =
			"Le numro de compte stock commence par 3";
	}

	if (designation === "" || !designation) {
		errors["designation"] = "La dsignation du produit est rquise.";
		valid = false;
	}

	if (unite === "" || !unite) {
		errors["unite"] = "La nature du produit est rquise.";
		valid = false;
	}

	if (marge === null || marge === undefined || marge <= 0) {
		errors["marge"] = "La marge bnficiaire du produit est rquise.";
		valid = false;
	}

	if (!qMin || qMin < 0) {
		errors["qMin"] = "Le stock de scurit du produit est rquis.";
		valid = false;
	}

	if (!code || code === "") {
		errors["code"] = "Le code du produit est rquis.";
		valid = false;
	}

	return {
		valid,
		errors,
	};
};


/**
 * Valide les donnes de la cration du stock initial
 * @returns Object of {boolean valid value and an objet of errrors}
 */
export const validerEntree = ({
	dateExp,
	quantite,
	cout_unitaire,
	taux,
	pv_unitaire,
	devise,
	provenance,
	licence_url,
	factureId,
	produit,
}) => {
	let valid = true;
	const errors = {};
	const defaultValueErrors = [];

	if (!produit) {
		errors["produit"] = "Slctionnez un produit.";
		valid = false;
	} else if (!produit.id) {
		errors["produit"] = "Slctionnez un produit.";
		valid = false;
	}

	if (quantite < 0 || !quantite) {
		errors["quantite"] = "La quantit du produit est rquise.";
		valid = false;
	}

	if (cout_unitaire < 0 || !cout_unitaire) {
		errors["cu"] = "Le cot unitaire du produit est rquis.";
		valid = false;
	}

	if (pv_unitaire < 0 || !pv_unitaire) {
		errors["pv"] = "Le prix de vente unitaire du produit est rquis.";
		valid = false;
	}

	if (taux < 0 || !taux) {
		defaultValueErrors.push("Le taux est rquis.");
		valid = false;
	}

	if (provenance < 0 || !provenance) {
		defaultValueErrors.push(
			"Veillez spcifier la source de l'entre en stock"
		);
		valid = false;
	}

	if (provenance === "licence" && (!licence_url || licence_url === "")) {
		defaultValueErrors.push("Ajouter l'image de la licence d'importation");
		valid = false;
	}

	if (provenance === "facture" && (!factureId || factureId === "")) {
		defaultValueErrors.push(
			"Veillez slctionner la facture d'achat pour ce produit."
		);
		valid = false;
	}

	if (devise === "" || !devise) {
		defaultValueErrors.push("Veillez slctionner la devise utilise.");
		valid = false;
	}

	if (dateExp !== "" && dateExp) {
		if (
			!/^(?:(?:31(\/|-|\.)(?:0?[13578]|1[02]|(?:Jan|Mar|May|Jul|Aug|Oct|Dec)))\1|(?:(?:29|30)(\/|-|\.)(?:0?[1,3-9]|1[0-2]|(?:Jan|Mar|Apr|May|Jun|Jul|Aug|Sep|Oct|Nov|Dec))\2))(?:(?:1[6-9]|[2-9]\d)?\d{2})$|^(?:29(\/|-|\.)(?:0?2|(?:Feb))\3(?:(?:(?:1[6-9]|[2-9]\d)?(?:0[48]|[2468][048]|[13579][26])|(?:(?:16|[2468][048]|[3579][26])00))))$|^(?:0?[1-9]|1\d|2[0-8])(\/|-|\.)(?:(?:0?[1-9]|(?:Jan|Feb|Mar|Apr|May|Jun|Jul|Aug|Sep))|(?:1[0-2]|(?:Oct|Nov|Dec)))\4(?:(?:1[6-9]|[2-9]\d)?\d{2})$/.test(
				dateExp
			)
		) {
			errors["dateExp"] = "Mauvaise date";
			valid = false;
		}
	}

	return {
		valid,
		errors,
		defaultValueErrors,
	};
};
